import uniSegmentedControl from "@/components/uni-segmented-control/uni-segmented-control.vue"
export default {
	components: {
		uniSegmentedControl
	},
	data() {
		return {
			bookList: [],
			journalList: [],
			items: ['主页', '图书', '杂志', '课程'],
			current: 0,
			requiredBooks: {
				"page_index": 1,
				"page_size": 10,
				"table_id": "13"
			},
			requiredJournal: {
				"page_index": 1,
				"page_size": 10,
				"table_id": "13",
				"resource_type": 2
			}
		}
	},
	onLoad(e) {
		let isVip = e.joinedState;
		if (isVip == 'init') {
			uni.showModal({
				title: '提示',
				showCancel: false,
				confirmText: '关闭',
				content: '您已申请企业会员，请等待审核。',
				success: function(res) {
					if (res.confirm) {
						console.log('用户点击关闭');
					}
				}
			});
		} else if (isVip == 'notVip') {
			uni.showModal({
				title: '提示',
				confirmText: '免费申请',
				cancelText: '我先看看',
				content: '为了更好的享受企业服务，请先申请为企业会员',
				success: function(res) {
					if (res.confirm) {
						console.log('用户点击确定');
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		} else if (isVip == 'pass') {
			console.log('hello vip')
		}

	},
	methods: {
		onClickItem(index) {
			//console.log(index)
			if (this.current !== index) {
				this.current = index;
			}
			if (index == 1) {
				console.log('111')
				uni.request({
					url: 'http://192.168.0.210:9999/book/book/page',
					method: 'POST',
					data: this.requiredBooks,
					header: {
						'content-type': 'application/json'
					},
					success: res => {
						this.bookList = res.data.data.pageBooks;
					},

				});
			}else if(index == 2){
					console.log('222')
					uni.request({
						url: 'http://192.168.0.210:9999/book/book/page',
						method: 'POST',
						data: this.requiredJournal,
						header: {
							'content-type': 'application/json'
						},
						success: res => {
							console.log(res.data.data.pageBooks);
							this.journalList = res.data.data.pageBooks;
							
						},
					
					});
			}
		},
		toEnterprise() {
			uni.reLaunch({
				url: '../index/enterprise/enterprise'
			});
		}
	}
}
